.product {
  &__photo {
    cursor: pointer;
    width: 100%;
  }
  &__title {
    font-size: 18px;
    font-weight: 700;
    line-height: 25.2px;
    text-align: left;
  }
  &__category {
    font-size: 16px;
    font-weight: 400;
    line-height: 22.4px;
    letter-spacing: 0.15px;
    text-align: left;
  }
  &__price {
    font-size: 16px;
    font-weight: 700;
    line-height: 22.4px;
    text-align: left;
  }
}
